.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error {
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 1;
    text-align: center;

    thead {
        background-color: $verde;

        th {
            color: $blanco;
            padding: 2rem;
        }
    }

    tbody {
        background-color: $gris;

        tr {
            color: $grisOscuro;

            td {
                padding: 2rem;
            }
        }
    }

    .imagen-tabla {
        width: 15rem;
        display: inline;
    }
}

.imagen-small {
    width: 20rem;
}